.about {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  position: relative;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 0.5;
  height: fit-content;
}

.about-description {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 0.5;
  height: fit-content;
}

.about-image {
  display: flex;
  align-items: center;
  justify-content: center;
  height: fit-content;
}

.about-image img {
  height: 11em;
  width: 11em;
  object-fit: cover;
  border-radius: 50%;
}

.about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 50px;
}

.about-contact {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.about-button {
  display: flex;
  justify-content: start;
  align-items: flex-start;
  margin-top: 1em;
}

.about-links {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-left: 4em;
  margin-top: 1.5em;
}

.about-links a {
  margin: 0 0.25rem;
}

.about-links a img {
  width: 1.5rem;
  height: 1.5rem;
}
